import { Component, OnInit } from '@angular/core';
import { HttpClient } from "@angular/common/http";

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})

export class HomeComponent implements OnInit {

  players: PlayerInfo[] = [];
  allKills: number = 0;

  constructor(private http: HttpClient) { }

  ngOnInit(): void {
    this.http.get<any>('https://cod-mw-wz-api.herokuapp.com/getfullmatch').subscribe(data => {
      data['allPlayers'].forEach(element => {
        let pl = new PlayerInfo(element['player']['username'], 
                                element['playerStats']['kills'],
                                element['playerStats']['teamPlacement'],
                                element['player']['team'],
                                element['matchID'], 
                                element['duration']
                                );
        this.players.push(pl);
        this.allKills += parseInt(pl.kills);
      });

      this.players.sort((a,b)=> {
        if(a.teamPlacement > b.teamPlacement) return 1;
        if(a.teamPlacement < b.teamPlacement) return -1;
        return 0
      });
    });
  }
}

class PlayerInfo {
  username: string;
  kills: string;
  teamName: string;
  teamPlacement: number;
  matchId: string;
  duration: string;
  
  constructor(username: string, kills: string, teamPlacement: number, teamName: string,  matchId: string, duration: string){
    this.username = username;
    this.kills = kills;
    this.teamPlacement = teamPlacement;
    this.teamName = teamName;
    this.matchId = matchId;
    this.duration = duration;
  }
}